import React from "react";
import { IPill } from "./types";
import classNames from "classnames";

const Pill: React.FC<IPill> =({
    title,
    color,
}) => {
    const pillClass = classNames(
        'text-white text-sm font-medium leading-none my-0 mx-0 mb-1 p-1.5 table rounded',
        {
            'bg-red-600 text-white': color === 'red',
            'bg-green-600 text-white': color === 'green',
            'bg-blue-600 text-white': color === 'blue',
            'bg-yellow-600 text-white': color === 'yellow',
        }
    )
    return (
        <div className='bg-red-600 text-white rounded-lg px-3 py-1 text-sm font-medium mr-2 inline-flex items-center justify-center;'>
            {title}
        </div>
    )
}

export default Pill;